.form_wrapper {
	position: relative;
	max-width: 584px;
	top: 0;
	left: 50%;
	transform: translateX(-50%);
	background: #fff;
	border-radius: 26px;
	box-shadow: 0 0 15px 8px #f7f9ff;
	padding: 50px 57px;
	width: 100%;
	float: left;
}
form.step_form_col {
	padding: 0;
	float: left;
	width: 100%;
}

.step_form_col .custom_field {
	display: flex;
	flex-flow: column-reverse;
	width: 100%;
	float: left;
}

.step_form_col .custom_field label,
.step_form_col .custom_field input {
	transition: all 0.2s;
	touch-action: manipulation;
}

.step_form_col .custom_field input {
	font-size: 14px;
	border: 0;
	border: 1px solid #9B9B9B;
	font-family: inherit;
	-webkit-appearance: none;
	border-radius: 0;
	padding: 0;
	cursor: text;
	height: 52px;
	border-radius: 5px;
	padding: 0 15px;
}

.step_form_col .custom_field input:focus {
	outline: 0;
	border-bottom: 1px solid #53c251;
}

.step_form_col .custom_field input:placeholder-shown + label {
	cursor: text;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	transform-origin: left bottom;
	transform: translate(5px, 38px) scale(1.2);
	font-weight: 500;
	float: left;
	padding: 0 10px;
	margin-bottom: 0;
}

.auto-selection .topic_list .active{
  background: #53c251;
  color: #fff;
}
.auto-selection .custom_field{
  position: relative;
}
.auto-selection .custom_field i {
  bottom: 17px;
  position: absolute;
  right: 21px;
  line-height: 18px;
  margin: auto;
  color: #53c251;
  cursor: pointer;
}

.step_form_col .custom_field ::-webkit-input-placeholder {
	opacity: 0;
	transition: inherit;
}

.step_form_col .custom_field input:focus::-webkit-input-placeholder {
	opacity: 1;
}

.step_form_col .custom_field input:not(:placeholder-shown) + label,
.step_form_col .custom_field input:focus + label {
	transform: translate(5px, 10px) scale(1);
	cursor: pointer;
	background: #fff;
	margin-bottom: 0;
	padding: 0 10px;

	font-weight: 500;
	max-width: fit-content;
}

