ul.progress_bar {
	width: 100%;
	float: left;
	margin: 0 0 40px 0;
	padding: 0;
}
ul.progress_bar li {
	width: calc(33% - 8px);
	float: left;
	list-style: none;
	position: relative;
}
ul.progress_bar li:after {
	position: absolute;
	content: '';
	width: 100%;
	height: 6px;
	background: #e9e9e9;
	top: 12px;
	left: 0;
}
ul.progress_bar li span {
	display: inline-block;
	width: 28px;
	height: 28px;
	position: relative;
}
ul.progress_bar li span:before {
	position: absolute;
	content: '';
	width: 28px;
	z-index: 999;
	height: 28px;
	background: #fff;
	border: 4px solid #eaeaea;
	border-radius: 100%;
	left: 0;
}
ul.progress_bar li:last-child {
	width: 27px !important;
}
ul.progress_bar li.active span:before {
	box-shadow: 0px 2px 8px rgba(0, 0, 0, .4);
	border: 4px solid #fff;
}
ul.progress_bar li.active span:after,
ul.progress_bar li.completed span:after {
	position: absolute;
	content: '';
	background: #53c251;
	width: 10px;
	height: 10px;
	z-index: 999;
	border-radius: 100%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

ul.progress_bar li.completed span:after {
	content: '\2713';
	color: #fff;
	background: #53c251;
	width: 100%;
	height: 100%;
	line-height: 27px;
	text-align: center;
}

ul.progress_bar li.completed:after {
	background: #53c251;
}

.slider {
	-webkit-appearance: none;
	width: 100%;
	height: 15px;
	border-radius: 5px;
	background: #d3d3d3;
	outline: none;
	opacity: 0.7;
	-webkit-transition: .2s;
	transition: opacity .2s;
}

.slider:hover {
	opacity: 1;
}

.slider::-webkit-slider-thumb {
	-webkit-appearance: none;
	appearance: none;
	width: 25px;
	height: 25px;
	border-radius: 50%;
	background: #4caf50;
	cursor: pointer;
}

.slider::-moz-range-thumb {
	width: 25px;
	height: 25px;
	border-radius: 50%;
	background: #4caf50;
	cursor: pointer;
}
