.togle-switch  .switch {
  position: relative;
  display: inline-block;
  width: 70px;
  height: 34px;
}

.togle-switch .switch input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.togle-switch .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.togle-switch .slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: #40c252;
  -webkit-transition: .4s;
  transition: .4s;
}

.togle-switch input:checked + .slider {
  background: #9bdfa4 !important;
}

.togle-switch input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

.togle-switch input:checked + .slider:before {
  -webkit-transform: translateX(35px);
  -ms-transform: translateX(35px);
  transform: translateX(35px);
}


.togle-switch .slider.round {
  border-radius: 34px;
  height: 33px;
}

.togle-switch .slider.round:before {
  border-radius: 50%;
}
